.contact {
    &-shake {
        animation: 3s shake infinite alternate;
    }
    &-icon {
        width: 75px;
        height: 75px;
        background-image: url("../images/info.png");
        background-size: cover;
    }
    &-social-media {
        font-size: 3.5vh;
        cursor: pointer;
    }
}

@media (min-width: 1024px) {
    .contact {
        &-icon {
            width: 20vh;
            height: 20vh;
        }
    }
}

/*Animation Shake*/
@keyframes shake {
    0% {
        transform: skewX(-3deg);
    }
    1% {
        transform: skewX(3deg);
    }
    2% {
        transform: skewX(-3deg);
    }
    3% {
        transform: skewX(3deg);
    }
    4% {
        transform: skewX(-3deg);
    }
    5% {
        transform: skewX(0);
    }
    100% {
        transform: skewX(0);
    }
}
