.pages {
    &-banner {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        &__headline {
            height: 50%;
            font-size: 35px;
        }
    }

    &-content {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        font-size: 18px;
        padding-left: 30px;
        padding-right: 30px;

        &__headline {
            height: 50%;
            font-size: 24px;
            font-weight: 600;
            padding-bottom: 30px;
        }

        &__sub-headline {
            padding-bottom: 30px;
        }
    }
}

@media (min-width: 1024px) {
    .pages {
        &-icon {
            width: 250px;
            height: 250px;
        }
        &-border {
            border-left: 6px solid black;
            border-radius: 2px;
            height: 80%;
            margin-left: 30px;
        }
        &-banner {
            height: 80%;
            padding-left: 30px;

            &__headline {
                font-size: 7vh;
            }
            &__sub-headline {
                color: #666;
                font-size: 1.75vh;
                padding-bottom: 30px;
            }
        }
        &-content {
            font-size: 2.5vh;
            padding-right: 40px;

            &__headline {
                height: 50%;
                font-size: 4.25vh;
                padding-bottom: 30px;
            }
        }
    }
}
