.modal {
    width: 100%;
    height: 100%;
    background-color: rgba($color: #000000, $alpha: 0.6);
    position: fixed;
    z-index: 12;
    display: none;
    justify-content: center;
    align-items: center;
    &-container {
        background-color: #ddd;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 15px;
        height: 250px;
        width: 100%;
    }
    &-headline {
        font-size: 2.5vh;
        padding-top: 30px;
    }
    &-redirect {
        display: flex;
        justify-content: space-between;
        font-size: 2.25vh;
        &--close {
            cursor: pointer;
        }
    }
}

@media (min-width: 768px) {
    .modal {
        &-container {
            width: 500px;
        }
        &-headline {
            font-size: 2.75vh;
        }
        &-redirect {
            font-size: 2.5vh;
        }
    }
}
@media (min-width: 1024px) {
    .modal {
        &-container {
            width: 658px;
        }
        &-headline {
            font-size: 3vh;
        }
        &-redirect {
            font-size: 2.75vh;
        }
    }
}
