.navigation__item {
    display: none;
}

#nav-icon {
    height: 45px;
    margin: 15px;
    position: absolute;
    transform: rotate(0deg);
    transition: 0.2s ease-in-out;
    width: 60px;
    z-index: 11;
    cursor: pointer;
    @media (min-width: 1024px) {
        display: none;
    }
    span {
        display: block;
        position: absolute;
        height: 5px;
        width: 100%;
        background: #333;
        border-radius: 7px;
        opacity: 1;
        left: 0;
        transform: rotate(0deg);
        transition: 0.33s ease;

        &:nth-child(1) {
            top: 0px;
        }

        &:nth-child(2),
        &:nth-child(3) {
            top: 18px;
        }

        &:nth-child(4) {
            top: 36px;
        }
    }
    &.burger-menu--open {
        span {
            background: #ddd;
            &:nth-child(1) {
                top: 18px;
                width: 0%;
                left: 50%;
            }
            &:nth-child(2) {
                transform: rotate(45deg);
            }
            &:nth-child(3) {
                transform: rotate(-45deg);
            }
            &:nth-child(4) {
                top: 18px;
                width: 0%;
                left: 50%;
            }
        }
    }
}

.navigation--mobile {
    list-style: none;
    &__navigation {
        background: #456789;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100%;
        margin: 0;
        padding: 0;
        position: absolute;
        transition: all 0.5s ease;
        transform: translateX(-100%);
        width: 100%;
        z-index: 10;
        &--open {
            transform: translateX(0);
        }
    }
    &__list {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        height: 60%;
        padding: 0;
        &--item {
            font-size: 30px;
            padding: 30px;
            transition: all 0.33s ease;
            &:hover {
                font-size: 32px;
                text-decoration: underline;
            }
        }
    }

    &__link {
        color: black;
    }
}

@media (min-width: 1024px) {
    .navigation--mobile {
        display: none;
    }
    .navigation {
        position: fixed;
        right: 75px;
        padding-top: 45px;
        z-index: 3;

        &__item {
            display: block;
            font-size: 4vh;
            padding-top: 14px;
            z-index: 2;
            & > a {
                position: relative;
                z-index: 2;
                text-decoration: none;
                color: #333;
            }

            &:hover .navigation-block {
                position: relative;
                width: 100%;
                background: #cdcdcd;
                bottom: -30px;
                z-index: 1;
            }
        }
        &-block {
            transform: translateY(1vh);
            @media (min-height: 1600px){
                transform: translateY(3vh);
            }
            height: 2vh;
            width: 0%;
            transition: width 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
        }
    }
}
