.codebits {
    &__container {
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
    &-icon {
        width: 75px;
        height: 75px;
        background-image: url("../images/code.png");
        background-size: cover;
    }
    &__skill {
        display: flex;
        border-radius: 0 0 0 10px;
        padding-top: 16px;
        transition: all 1.5s ease;

        &:hover {
            animation-name: skillBounce;
            animation-duration: 1s;
            animation-delay: 0.2s;
            @keyframes skillBounce {
                0% {
                    transform: translateX(0);
                }
                25% {
                    transform: translateX(15px);
                }
                50% {
                    transform: translateX(0);
                }
                65% {
                    transform: translateX(5px);
                }
                100% {
                    transform: translateX(0);
                }
            }

            .codebits-technologies {
                background: #bcfbfc;
                transition: all 1s ease;
            }
        }

        &--bad {
            background: radial-gradient(
                circle at 0% 50%,
                rgba(255, 255, 255, 0) 0%,
                rgba(255, 255, 255, 0) 50%,
                #aa0000 50%,
                #aa0000 100%
            );
            height: 30px;
            width: 40px;
            position: relative;
            margin-left: -15px;
            &::before {
                content: "";
                position: absolute;
                left: 15px;
                bottom: -1px;
                height: 29px;
                width: 25px;
                border-bottom: 1px solid black;
                border-right: 1px solid black;
                border-top: 1px solid black;
            }
        }
        &--medium {
            background: #cc9933;
            border: 1px solid black;
            height: 30px;
            width: 20px;
            margin-left: 5px;
            margin-right: 5px;
        }
        &--good {
            background: #33cc33;
            border: 1px solid black;
            height: 30px;
            width: 20px;
            margin-left: 5px;
            margin-right: 5px;
        }
        &--very-good {
            background: #66ff66;
            border: 1px solid black;
            height: 30px;
            width: 20px;
            margin-left: 5px;
            margin-right: 5px;
        }
    }
    &-headline {
        font-size: 36px;
        color: #222;
    }
    &-sub-headline {
        font-size: 18px;
    }
    &-technologies {
        background: #ddd;
        border: none;
        border-radius: 15px;
        cursor: pointer;
        font-size: 16px;
        padding: 5px 15px;
        text-align: center;
        text-decoration: none;
    }
}

@media (min-width: 1024px) {
    .codebits {
        &__container {
            padding: 30px;
        }
        &-headline {
            font-size: 48px;
        }
    }
}
