.base-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
}
.base-container--reverse {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
}

@media (min-width: 1024px) {
    .base-container {
        flex-direction: row;
        align-items: center;
        justify-content: start;
        width: calc(100% - 240px);
        padding-left: 30px;
    }
    .base-container--reverse {
        flex-direction: row;
        justify-content: start;
        width: calc(100% - 240px);
        padding-left: 40px;
        padding-right: 40px;
        margin-left: 240px;
    }
}
